@import url(https://fonts.googleapis.com/css?family=EB+Garamond|Fira+Sans|Lora|Open+Sans|PT+Serif|Quattrocento|Roboto);
.styles_Wrapper__QmsU2 {
  display: grid;
  grid-template-columns: 10rem 1fr;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

.styles_WrapperTitleBar__1uVIm {
  grid-template-areas:
    "title title"
    "sidebar preview"
    "status status";
  grid-template-rows: 25px 1fr 25px;
}

.styles_WrapperTopBar__3TDGm {
  grid-template-areas:
    "topbar topbar"
    "sidebar preview"
    "status status";
  grid-template-rows: 6mm 1fr 5mm;  
}

.styles_PreviewOuter__aClrK {
  grid-area: preview;
  flex: 1 1 100%;
  overflow: scroll;
}

.styles_PreviewInner__2lsw- {
  height: 130vh;
}

.styles_SideBar__11KBm {
  grid-area: sidebar;
  display: flex;
  flex: 1 1 100%;
  flex-direction: column;
  /*background-image: linear-gradient(90deg, #ddd, 5%, #f0f0f0, 95%, #ddd);*/
  background-color: #f5f5f5;
  border-right: 1px solid #ccc;
  overflow: clip;
  padding: 2mm;
}

.styles_SideBar__11KBm > * {
  margin: 2px;
  top: auto;
  bottom: auto;
  left: auto;
  right: auto;
}

.styles_StatusBar__3LGjN {
  grid-area: status;
  /*background-image: linear-gradient(0deg, #ddd, 25%, #f0f0f0, 99%, #ddd);*/
  background-color: #f5f5f5;
  border-top: 1px solid #ccc;
  display: flex;
  padding-left: 2mm;
  padding-right: 2mm;
  flex: 1 1 100%;
  align-items: center;
  justify-content: right;
  font-size: 0.75rem;
}

.styles_StatusBar__3LGjN > * {
  top: auto;
  bottom: auto;
  left: auto;
  right: auto;
}

.styles_TitleBar__315t0 {
  grid-area: title;
  background-image: -webkit-linear-gradient(bottom, #ddd, 25%, #ededed, 99%, #ddd);
  background-image: linear-gradient(0deg, #ddd, 25%, #ededed, 99%, #ddd);
  display: flex;
  flex: 1 1 100%;
  align-items: center;
  justify-content: center;
  -webkit-user-select: none;
  -webkit-app-region: drag;
}

.styles_TopBar__I1VNN {
  grid-area: topbar;
  background-color: #f5f5f5;
  border-bottom: 1px solid #ccc;
  display: flex;
  flex: 1 1 100%;
  align-items: center;
  justify-content: center;
  font-size: 0.75rem;
}

.styles_TopBar__I1VNN > * {
  bottom: auto;
  top: auto;
}


@media print {
  .styles_Wrapper__QmsU2 {
    display: block;
  }

  .styles_PreviewOuter__aClrK {
    overflow: visible;
  }
  
  .styles_SideBar__11KBm {
    display: none;
  }

  .styles_StatusBar__3LGjN {
    display: none;
  }

  .styles_TopBar__I1VNN {
    display: none;
  }

  .styles_TitleBar__315t0 {
    display: none;
  }
}
.Settings {
  height: 100vh;
  width: 100vw;
  margin: 0 0;
  background-color: #ededed;
  padding: 0.5rem 0.5rem;
}


.Modal {
  position: absolute;
  margin: 2rem;
  border-radius: 0.5rem;
  background-color: #fafafa;
  outline: none;
  padding: 1rem;
  border: 1px solid rgba(0,0,0,0.5);
}

.Overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(100,100,100,0.3);
}
@page { margin: 0 }
body { margin: 0 }
.sheet {
  margin: 0;
  overflow: hidden;
  position: relative;
  box-sizing: border-box;
  page-break-after: always;
}

/** Paper sizes **/
div.A3               .sheet { width: 297mm; height: 419mm }
div.A3.landscape     .sheet { width: 420mm; height: 296mm }
div.A4               .sheet { width: 210mm; height: 296mm }
div.A4.landscape     .sheet { width: 297mm; height: 209mm }
div.A5               .sheet { width: 148mm; height: 209mm }
div.A5.landscape     .sheet { width: 210mm; height: 147mm }
div.letter           .sheet { width: 216mm; height: 279mm }
div.letter.landscape .sheet { width: 280mm; height: 215mm }
div.legal            .sheet { width: 216mm; height: 356mm }
div.legal.landscape  .sheet { width: 357mm; height: 215mm }

/** Padding area **/
.sheet.padding-10mm { padding: 10mm }
.sheet.padding-15mm { padding: 15mm }
.sheet.padding-20mm { padding: 20mm }
.sheet.padding-25mm { padding: 25mm }

/** For screen preview **/
@media screen {
  html, body { background-color: #aaa }
  .sheet {
    background: white;
    box-shadow: 0 .5mm 2mm rgba(0,0,0,.3);
    margin: 5mm auto;
  }
}

/** Fix for Chrome issue #273306 **/
@media print {
           div.A3.landscape { width: 420mm }
  div.A3, div.A4.landscape { width: 297mm }
  div.A4, div.A5.landscape { width: 210mm }
  div.A5                    { width: 148mm }
  div.letter, div.legal    { width: 216mm }
  div.letter.landscape      { width: 280mm }
  div.legal.landscape       { width: 357mm }
}

:root {
  --docFontFamily: 'pt serif';
  --docFontSize: 9pt;
  --editColorSolid: rgba(0,200,120,0.5);
  --editColorFaded: rgba(0,200,120,0.05);
}

.katex-html {
  white-space: nowrap;
  font-size: 3.5mm;
}

blockquote {
  font-style: italic;
}

/* TABLES */

table {
  border-collapse: collapse;
}

th {
  font-weight: bold;
}

th, td {
  border: 1px solid black;
  padding: 0.2rem 0.5rem;
}

.Document {
  font-family: serif;
  font-family: var(--docFontFamily, serif);
  font-size: 3.5mm;
  font-size: var(--docFontSize, 3.5mm);
}

.AnswerChoices {
  padding-left: 5mm;
}

.AnswerChoice {
  margin: 2mm;
}

.QuestionWrapper {
  display: grid;
  grid-gap: 0.5rem;
  margin-bottom: 0.8rem;
  page-break-inside: avoid;
  -webkit-column-break-inside: avoid;
          break-inside: avoid;
}

/*
.QuestionWrapper:hover {
  outline: 1px solid var(--editColorSolid);
}
.QuestionWrapper > div:hover {
  background-color: var(--editColorFaded);
  outline: 1px solid var(--editColorSolid);
}
*/

.QuestionSet > ol {
  padding-left: 5mm;
}

.Heading {
  border-collapse: collapse;
  width: 100%;
  font-size: 1.05rem;
  font-weight: bold;
}

.HeadingBlank {
  display: inline-block;
  border-bottom: 1px solid black; 
  margin: 0 1mm;
}

.Heading > tbody > tr > * {
  border: none;
}

.Heading > tbody > tr > td {
  padding-bottom: 3mm;
}

.Heading > tbody > tr > td:first {
  text-align: left;
}

.Heading > tbody > tr > td:last-child {
  text-align: right;
  right: 0;
}

.Heading:hover {
  outline: 1px solid var(--editColorSolid);
}

.Editor:hover {
  outline: none;
}

html, body {
  height: 100%;
  width: 100%;
  margin: 0;
}

body {
  height: 100%;
  width: 100%;
  margin: 0;
  font-family: sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: monospace;
}
